* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  max-width: 320px;
  margin-bottom: 16px;
  padding: 8px;
}

.country-list {
  list-style: none;
}

.country-list__item {
  flex-wrap: wrap;
  column-gap: 10px;
  display: flex;
}

.country-list__item:not(:last-child) {
  margin-bottom: 10px;
}

.country-list__flag {
  width: 30px;
  height: 24px;
  object-fit: contain;
  display: block;
  box-shadow: 0 8px 24px rgba(149, 157, 165, .2);
}

.country-list__name {
  text-shadow: 3px 4px 7px rgba(81, 67, 21, .8);
  margin: 0;
  font-weight: 600;
}

.country-info {
  width: 50%;
  text-align: center;
  color: red;
  text-shadow: -1px 1px #41ba45, 1px 1px #c63d2b, 1px -1px #42afac, -1px -1px #c6c23f;
  margin: 0 auto;
  padding: 40px;
  font-weight: 800;
  position: relative;
}

.country-info__heading {
  font-size: 48px;
  font-weight: 400;
}

.country-info__flag {
  z-index: -1;
  width: 100%;
  height: 100%;
  object-fit: cover;
  background: linear-gradient(90deg, #020024 0%, rgba(0, 0, 0, .376) 100%, #00d4ff 100%);
  display: block;
  position: absolute;
  box-shadow: 3px 4px 7px rgba(81, 67, 21, .8);
}

.country-info__text {
  font-size: 20px;
}

.country-info__information {
  font-size: 24px;
}

